<template>
        <el-container>
            <el-header style="height:40px;" class="miniapp-here">
                当前位置: <router-link to="/travel">首页</router-link> >
                <router-link to="/travel/user">客户管理</router-link> >
                 <router-link to="/travel/user/create">新增会员</router-link>
             </el-header>
            <el-main  class="miniapp-main">
                <div class="miniapp-content">
                        <el-form size="mini" :model="ruleForm" :rules="rules" ref="ruleForm" label-width="120px" style="padding:20px;">
                            <el-form-item label="会员手机" prop="mobile">
                                <el-input size="small" style="width:300px;" v-model="ruleForm.mobile"></el-input>请输入大陆11位手机号
                            </el-form-item>
                           <el-form-item label="会员昵称" prop="nickname">
                                <el-input size="small" style="width:200px;" v-model="ruleForm.nickname"></el-input>会员昵称
                            </el-form-item>
                            <el-form-item label="头像" prop="face">
                            </el-form-item>
                            <el-form-item label="OPENID">
                                <el-input size="small" style="width:200px;" v-model="ruleForm.open_id"></el-input>可以不填写，微信登录后会自动记录
                            </el-form-item>

                            <el-form-item label="是否锁定">
                                <el-switch v-model="ruleForm.is_lock"   :active-value="1"
                                        :inactive-value="0"	></el-switch>
                            </el-form-item>

                            <el-form-item>
                                <el-button type="primary" size="small" :loading="loading" @click="submitForm('ruleForm')">立即创建</el-button>
                                <router-link style="margin-left:10px;" to="/travel/user">返回首页</router-link>
                            </el-form-item>
                        </el-form>

                </div>
            </el-main>
        </el-container>
</template>
<script>
import * as api from "@/api.js";
import * as setting from "@/setting.js";


export default {
  name: "traveldestinationCreate",
  components: {

  },
  data: function() {
    return {
      loading: false,
       ruleForm: {
          mobile: '',
          is_lock:0,
          nickname:'',
          face:'',
          open_id:'',
        },
        rules: {
          mobile: [
            { required: true, message: '请输入会员手机号', trigger: 'blur' },
          ],
          nickname:[
              { required: true, message: '请输入会员昵称', trigger: 'blur' },
          ],
          face:[
              { required: true, message: '请上传会员头像', trigger: 'change' },
          ]
        }
    };
  },
  created() {

  },
  methods: {

      submitForm(formName) {
        if(this.loading ==true) return false;
        this.$refs[formName].validate((valid) => {
          if (valid) {
                var  that = this;
                that.loading = true;
                api.post('app/user.user/add',{
                    appauth: that.$store.getters.getApp.app_auth,
                    datas:that.ruleForm,
                }).then(res=>{
                    that.loading = false;
                    that.$message({
                        message:'添加会员成功',
                        type:'success',
                    });
                    that.$router.replace('/travel/user');
                }).catch((error) => {
                    that.loading = false;
                });

          } else {
            return false;
          }
        });
      },

  }
};
</script>
